define('chat-app-client/components/main-chat', ['exports', 'ember'], function (exports, _ember) {
  exports['default'] = _ember['default'].Component.extend({
    init: function init() {
      this._super();
      this.get('socket').on('welcome', this, 'welcome').on('online', this, 'online').on('joined', this, 'joined').on('chat', this, 'chat').on('left', this, 'left');
    },

    hideOrShow: 'hidden',
    msg: [],
    names: '',

    welcome: function welcome(msg) {
      this.set('hideOrShow', '');
      var msgDetail = {
        msg: msg,
        className: 'text-center'
      };
      this.msg.unshiftObject(msgDetail);
    },

    online: function online(connections) {
      var names = '';
      for (var i = 0; i < connections.length; ++i) {
        if (connections[i].user) {
          if (i > 0) {
            if (i === connections.length - 1) names += ' and ';else names += ', ';
          }
          names += connections[i].user.name;
        }
      }
      this.set('names', names);
    },

    joined: function joined(user) {
      var msgDetail = {
        msg: user.name + ' joined the chat',
        className: 'text-center'
      };
      this.msg.unshiftObject(msgDetail);
    },

    left: function left(user) {
      var msgDetail = {
        msg: user.name + ' left the chat',
        className: 'text-center'
      };
      this.msg.unshiftObject(msgDetail);
    },

    chat: function chat(msg) {
      var msgDetail = {
        msg: msg.user.name,
        className: 'alert alert-success',
        text: msg.message
      };
      this.msg.unshiftObject(msgDetail);
    },

    actions: {
      sendMsg: function sendMsg() {
        this.get('socket').emit('chat', this.get('newMsg'));
        console.log('Sending message: ', this.get('newMsg'));
        var msgDetail = {
          msg: this.get('newMsg'),
          className: 'alert alert-info text-right'
        };
        this.msg.unshiftObject(msgDetail);
        this.set('newMsg', '');
      }
    }

  });
});